import React from 'react'

function Result({data}) {

    return (
        <div id="result" className="ui two column equal width left aligned grid">
            <div className="row">
                <div className="six wide center aligned column">Word</div>
                <div className="column">{data.word}</div>
               
            </div>

            <div className="row">
                <div className="six wide center aligned column">Pronunciation</div>
                <div className="column" style={{color:'cyan'}}>{data.pronunciation}</div>
            </div>

            
            {
                data.definitions ?  data.definitions.map((e,i)=>(
                    <>
                    <div key={i} className="row">
                    <div className="six wide center aligned column">Definition {data.definitions.length > 1 && i+1}</div>
                    <div className="column" style={{color:'yellow'}}>{e.definition}</div>
                    <div className="six wide right aligned column">Type</div>
                    <div className="column" style={{color:'red'}}>{e.type}</div>
                   
                    {e.example ?  <><div className="six wide right aligned column">Example</div>
                    <div className="column" style={{color:'green'}}>{e.example}</div></> : ''}
                    <br/>
                    
                    {e.emoji ? <><div className="six wide right aligned column">Emoji</div>
                                <div className="column">{e.emoji}</div></> : ''}
                    
                    {e.image_url ? <><div className="six wide right aligned column">Image</div>
                    <div className="column"><img src={e.image_url} alt="word description"/></div></>: ''}
                    </div>
                    <hr/>
                    </>
                )) : ''
            }
           


        </div>
    )
}

export default Result
