import React from 'react'

function SearchInput({search, setSearch, callApi, setError}) {
    return (
        <>
        <div className="ui left icon input">
        <input 
        
        value={search} 
        onChange={(e)=>{
            setError(false);
            setSearch(e.target.value)}} 
        type="text" 
        placeholder="Search word..."
        onKeyPress={(e)=>{
           
            if(e.key==='Enter'){
                if(search) //search value shi mha
                callApi();
            }
        }}
        
        />
        <i className="quote left icon"></i>
      </div>
      </>
    )
}

export default SearchInput
