import React, { useState } from 'react';
import './index.css';
import Container from './components/Container';
import SearchInput from './components/SearchInput';
import Result from './components/Result';
import axios from 'axios';

function App() {
    
    const [data, setData] = useState({});
    const [loading, setLoading] = useState(false);
    const [search, setSearch] = useState('');
    const [error,setError] = useState(false);    
    
    async function callApi(){
       
        try{
            setError(false);
            setLoading(true);
            let response = await axios(`https://owlbot.info/api/v4/dictionary/${search}`,{
                headers:{
                    Authorization:"Token 75b267053a5ae018e55dc361c9c4cd1af0dfa8e4"
                }
            })
     
            setLoading(false);
            const api = response.data;
            setData(api);
        }
        catch(err){
            setError(true);
            setData({});
            setLoading(false);
        }
    };

    return (
        <Container>
            <SearchInput search={search} setSearch={setSearch} callApi={callApi} setError={setError}/>
            {
                loading ? 
                <div className="ui active dimmer">
                  <div className="ui loader"></div>
                </div>
             :  error ? <h1>{search} is not found!</h1> : Object.keys(data).length === 0 ? '': <Result data={data}/> 
            }
        </Container>
    )
}

export default App
