import React from 'react'

function Container({children}) {
    return (
        <div id="container">
            <h1>Owl Dictionary</h1>
            {children}
            <h6 style={{position:'absolute',bottom:'0',left:'0',right:'0'}}>Developed by Kyaw Zin Thant</h6>
        </div>
    )
}

export default Container
